form.edit-eoppep-form {
    h2.form-title {
      padding: 15px;
    }
    .form-group {
      margin-bottom: 1.2em;
      overflow: hidden;
     label {
      float: left;
      width: 28%;
      padding-right: 10px;
      padding-top: 8px;
      text-align: left;
    }
    .form-control {
      font-size: 16px;
      padding: 4px 8px;
      height: 36px;
      width: 72%;
      float: left;
      border-radius: 4px !important;
    }
      .form-control[disabled] {
      background-color: grey;
      opacity: 0.55;
        color: white;
        font-weight: bold;
      }
    }
    .less-p {
       label {
        padding-top: 0;
       }
    }
    #edit-sex--wrapper {
      padding-bottom: 15px;
      legend {
        float: left;
        width: 25%;
        font-size: 18px;
        font-weight: bold;
        border-style: none;
      }
      .fieldset-wrapper {
        float: left;
        width: 75%;
        #edit-sex {
          label {
            font-size: 16px;
            font-weight: bold;
          }
        }
      }
    }
   
  }